import React, { Component } from "react";

class Info extends Component {
  render() {
    return (
      <section id="about" style={{marginTop: "2rem"}}>
      <p style={{paddingBottom: "1rem"}} className="is-size-7 has-text-centered">*{this.props.t("info.small")}</p>
      <div className="section is-centered has-text-centered columns" style={{backgroundColor: "#00a6e1"}}>
      <div className="column is-5">
        <a href={"https://market.globatalent.com/register/?" + this.props.affiliateId}><h2 className="title has-text-white is-4">{this.props.t("info.title")}</h2></a>
      </div>
      </div>
      </section>
    )
  }
}

export default Info;